.content {
    width: 20vw;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap-reverse;
    gap: 40px;
    margin: 0 vw;
    margin-bottom: 50px;
}

.nope {
    width: 250px;
    text-align: center;
    font-size: 1rem;
    color: aliceblue;
    text-shadow: 2px 1px 3px rgba(61, 0, 173, 0.692), -2px -1px 3px rgba(61, 0, 173, 0.692);
    padding: 6px 50px;
    background-color: rgba(0, 0, 0, 0.801);
    border-radius: 20px;
}